

.column__generic{
    display: flex;
    height: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header__modal{
    margin: 10px;
    position: relative;
    width: 100%;
    height: 50px;
}

.layer_title{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.layer_ic_exit{
    position: absolute;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
}

.img_exit{
    width: 35px;
    height: 35px;
    margin-right: 10px;
}

.body__modal{
    width: 100%;
    height: 100%;
    overflow: auto;
    margin: 10px 10px 10px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
