.container-form{
    max-height: 400px;
    width: 500px;
    height: 400px;
    display: flex;
    flex-direction: column;
}

.form-add{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;
}

.column-titles{
    display: flex;
    flex-direction: column;
    margin-right: 15px;
}

.column-titles p{
    text-align: right;
    margin: 0px 0px 11px 0px;
}

.column-fields{
    display: flex;
    flex-direction: column;
}

.column-fields p{
    text-align: left;
    margin: 0px 0px 11px 0px;
    font-weight: lighter;
}

:focus{
    outline: 0;
}

.select-lis{
    width: 100%;
    margin-bottom: 10px;
}

.title-select{
    margin-top: 10px;
    font-size: 20px;
    font-weight: normal;
}