.container-login{
    width: 100%;
    height: 100%;
}

.main-login{
    width: 100%;
    height: 600px;
    margin-top: 64px;
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-body{
    height: auto;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.spacer{
    margin: 8px;
}

.btn-login{
    width: 150px;
    height: 40px;
    border-radius: 18px;
    color: #000;
    background-color: #51DCEC;
    text-decoration: none;
    box-shadow: none;
    margin-bottom: 25px;
    border-top: transparent !important;
    border-left: transparent !important;
    border-right: transparent !important;
    border-bottom: 4px solid !important;
}

.field-login::placeholder{
    text-align: center;
    color: #51DCEC;
}

.field-login{
    border: none;
    color: #51DCEC;
    text-align: center;
}

.btn-sign-here{
    border: none;
    background-color: transparent;
    color: #000;
    opacity: .7;
}

input:focus{
    outline: 0;
}

.text-title{
    font-size: 32px;
    text-align: center;
    font-weight: lighter;
}

.text-subtitle{
    font-weight: lighter;
    font-size: 20px;
    margin: 0;
}