.toolbar{
    z-index: 3;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 56px;
    background-color: #707070;
}

.toolbar_navigation{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}

.toolbar_logo h3{
    color: #51DCEC;
    text-decoration: none;
    margin: 0;
    margin-left: 10px;
    size: 0.5rem;
}

.toolbar_title h3{
    margin: 0px 0px 0px 0px;
    margin-left: 10px;
    color: #ffffff;
}

