.container-card-p{
    margin-bottom: 20px;
    margin-right: 20px;
    padding: 0;
    width: 110px;
    height: 100px;
    background-color: #17a2b8;
    display: flex;
    flex-direction: column;
    align-content: center;
    border-radius: 1%;
}

.sub-container-p{
    flex: 1;
    margin: 3px;
    position: relative;
}

.layer-opacity-p{
    z-index: -0.2;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    background-color: #707070;
    border-radius: 1%;
}

.info-card-p{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -0.1;
    padding: 3px;
    display: flex;
    flex-direction: column-reverse;
}

.row-ic-p{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.spacer-p{
    flex: 1;
}

.ic-edit-p{
    width: 20px;
    height: 20px;
    margin-bottom: 1px;
    margin-right: 3px;
}

.txt-p{
    font-size: 12px;
    color: #fff;
}

.txt-bold-p{
    font-size: 14px;
    font-weight: bold;
    color: #fff;
}
