.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px rgb(0,0,0,5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 300px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
    display: flex;
    flex-direction: column;
}

.side-drawer.open{
    transform: translateX(0);
}

.header-sidebar{
    width: 100%;
    height: 56px;
    background-color: #707070;
}

.container-info{
    width: 90%;
    max-width: 290px;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
}

.body-info{
    margin: 0 auto;
}

.ic-exit{
    width: 28px;
    height: 28px;
    margin-bottom: 5px;
}

.img-row{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.img-profile{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 7px solid #707070;
}

.txtNormal{
    text-align: center;
    size: 0.3rem;
    margin: 7px;
    padding: 0;
}

.low{
    opacity: 0.5;
}

.opt-sidebar{
    display: flex;
    flex-direction: column;
}

.opt-descrip{
    display: flex;
    flex-direction: row;
    justify-items: center;
}

.opt-descrip p {
    margin-left: 10px;
}

.opt-descrip img {
    width: 40px;
    height: 40px;
}

.opt-line {
    background-color: #707070;
    width: 100%;
    height: 3px;
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.rem 0;
}

.side-drawer a{
    text-decoration: none;
    font-size: 1.2rem;
    color: #521751;
}

.container-nav ul{
    margin: 0;
}

.container-nav ul li{
    margin-bottom: 5px;
}

.opt-descrip p :link{
    text-decoration: none;
    color: #51DCEC;
}

