.main-container-up{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 15px 15px;
  }

.form-up{
  display: flex;
    flex-direction: column;
    width: 100%;
}

.container-rows-up{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 5px;
}
  
  .inputfile-up{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  .inputfile-up + label {
    width: 200px;
    font-size: 1em;
    text-align: center;
    font-weight: 200;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color:  #fff;
    display: inline-block;
}

.inputfile-up:focus + label,
.inputfile-up + label:hover {
    background-color:  #51DCEC;
    color: #fff;
}
  
  .btn-upload{
    width: auto;
    height: 30px;
    border-radius: 18px;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    background-color: #fff;
    text-decoration: none;
    box-shadow: none;
    margin-top: 10px;
    margin-bottom: 15px;
  }
  
  textarea {
    margin: 20px auto;
    width: 200px;
  }

  .img-up{
    width:60px;
    height:60px;
    margin-left:20px;
    border: 1px solid #51DCEC;
  }

  .list-imgs-products{
    width: 100%;
    display: flex;
    flex-flow: wrap;
  }

  .row-imgs-up{
    margin: 0px 10px 0px 10px;
  }

.msg-up{
  width: 100%;
  height: 40px;
}
