.container-form{
    width: 400px;
    height: 380px;
    display: flex;
    flex-direction: column;
}

.form-add{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0;
}

.column-titles{
    display: flex;
    flex-direction: column;
    margin-right: 15px;
}

.column-titles p{
    text-align: right;
    margin: 0px 0px 11px 0px;
}

.column-fields{
    display: flex;
    flex-direction: column;
}

.column-fields p{
    text-align: left;
    margin: 0px 0px 11px 0px;
    font-weight: lighter;
}

:focus{
    outline: 0;
}
