.container-form{
    width: 400px;
    height: 380px;
    position: relative;
}

.layer-alert{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    position: absolute;
}

.main-form{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
}
.form-add{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    margin: 0;
}

.column-titles{
    display: flex;
    flex-direction: column;
    margin-right: 15px;
}

.column-titles p{
    text-align: right;
    margin-bottom: 11px;
}

.column-fields{
    display: flex;
    flex-direction: column;
}

.column-fields input {
    border: 0.5px solid #51DCEC;
    text-align: center;
    border-radius: 5px;
    margin-bottom: 8px;
}

.column-fields input::placeholder{
    text-align: center;
    color: #51DCEC;
}

:focus{
    outline: 0;
}

.row-select{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.row-select p {
    margin-top: 10px;
    margin-right: 15px;
}

.select-list{
    width: 100px;
}