.main-container-ig{
    display: flex;
    flex-direction: column;
}

.body-img-ig{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-img-ig{
    z-index: 1;
    border: 1px solid #51DCEC;
    margin: 0px 5px 1px 0px;
    width : 52px;
    height : 52px;
}

.btn-edit-ig{
    width: 52px;
    height: 30px;
    border-radius: 10%;
    border: 1px solid #51DCEC;
    color: #51DCEC;
    font-size: 14px;
    padding: 3px;
    background-color: #fff;
    text-decoration: none;
    box-shadow: none;
}

.row_delete_ig{
    display: flex;
    flex-direction: row-reverse;
    height: 5px;
    width: auto;
}

.ic_delete_ig{
    z-index: 2;
    padding: 2px;
    transform: translateY(-10px);
    transition: all 750ms;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 2px 2px 2px black;
    width: 20px;
    height: 20px;
}

.ic_delete_ig:hover{
    transform: scale(1.6);
}